// https://www.fontsquirrel.com/fonts/quicksand
@font-face {
    font-family: "Quicksand-Light";
    src: url("./Quicksand-Light.woff2") format("woff2"),
        url("./Quicksand-Light.woff") format("woff"); /* Modern Browsers */
    font-weight: normal;
    font-style: normal;
}
// regular
@font-face {
    font-family: "Quicksand-Regular";
    src: url("./Quicksand-Regular.woff2") format("woff2"),
        url("./Quicksand-Regular.woff") format("woff"); /* Modern Browsers */
    font-weight: normal;
    font-style: normal;
}
// Bold
@font-face {
    font-family: "Quicksand-Bold";
    src: url("./Quicksand-Bold.woff2") format("woff2"),
        url("./Quicksand-Bold.woff") format("woff"); /* Modern Browsers */
    font-weight: normal;
    font-style: normal;
}
